// fry 25e4c990e5c0b388c6b7a98cb3678f80c26f4edd
import { signal } from 'fryweb';

export const setup = async function () {
    let { vcenter, hcenter } = this.fryargs;
    
                const hbroaden = () => hcenter.broaden();
        const hnarrow = () => hcenter.narrow();
        const vbroaden = () => vcenter.broaden();
        const vnarrow = () => vcenter.narrow();
        const hcentered = signal(false);
        const vcentered = signal(false);
        const toggleHcenter = () => {
          if (hcentered.peek()) {
            hcentered.value = false;
            hcenter.setCentered(false);
          } else {
            hcentered.value = true;
            hcenter.setCentered(true);
          }
        }
        const toggleVcenter = () => {
          if (vcentered.peek()) {
            vcentered.value = false;
            vcenter.setCentered(false);
          } else {
            vcentered.value = true;
            vcenter.setCentered(true);
          }
        }
        document.addEventListener('keydown', (e) => {
          if (e.code === 'KeyB') {
            console.log(e.code);
            hbroaden();
            vbroaden();
          } else if (e.code === 'KeyN') {
            console.log(e.code);
            hnarrow();
            vnarrow();
          }
        })
    
    this.fryembeds = [hbroaden, hnarrow, toggleHcenter, hcentered, vbroaden, vnarrow, toggleVcenter, vcentered];
};
